  font-weight: bold;
}
.make-it-bold {
  font-weight: bold;
  text-decoration: underline;
}

.event-details {
  padding: 0 3%;
}

.event-details h1 {
  font-size: 36px;
}

.event-details h2 {
  font-size: 30px;
}

.event-list {
  font-size: 23px;
  padding-left: 25px;
}

.event-list li {
  padding-top: 10px;
}

.event-rounds {
  padding: 0 3%;
}

.event-rounds h3 {
