}

.description-list li h3 {
  font-weight: bold;
  font-size: 25px;
  margin-top: 15px;
}

.description-list li {
  margin-top: 10px;
}

.timeline-heading {
  margin-top: 30px;
  font-size: 70px;
  font-weight: bold;
  padding-left: 40rem;
}

.timeline {
  margin: 0 auto;
  max-width: 80%;
  padding: 25px;
  display: grid;
  grid-template-columns: 1fr 3px 1fr;
  color: #000;
}

.timeline__component {
  margin: 20px 30px 20px 20px;
}

.timeline__component--bg {
  padding: 1.5em;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

/* LEAVE TILL LAST */
.timeline__component--bottom {
  margin-bottom: 0;
}

.timeline__middle {
  position: relative;
  background: darkgray;
}

.timeline__point {
  position: absolute;
  top: 23px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 15px;
  height: 15px;
  background: white;
  border: 2px solid grey;

  border-radius: 50%;
}

/* LEAVE TILL LAST */
.timeline__point--bottom {
  top: auto;
  top: initial;
  bottom: 0;
}

.timeline__date--right {
  text-align: right;
}

.timeline__title {
  margin: 0;
  font-size: 23px;
  color: #363636;
  font-weight: bold;
  text-align: center;
}

.timeline__paragraph {
  margin-top: 9px;
  line-height: 1.5;
  text-align: left;
  font-size: 20px;
}
.timeline__date {
  font-size: 23px;
  font-weight: bold;
  color: #363636;
}
/*********** Techathon *********/
.contents {
  padding: 5%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  max-width: 100%;
}

.speakers-grid {
  display: grid;
  grid-template-columns: repeat(3, 2fr);
  align-items: center;
  justify-content:space-between;
  grid-row-start: auto;
  grid-row-end: auto;
  grid-row-gap: 20px;
  row-gap: 20px;

}
.speaker-pic
{
  border:none;
  height: auto;
  max-height: 350px;
  width: 100%;
  

}
.speaker-card
{
  width:326px;
  height:500px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color:#000;
  margin-left: auto;
  margin-right: auto;
  border-radius: 15px 15px 0px 0px;
  
  
}
.speaker-pic-info{
  position: relative;
}
.speaker-pic
{
  width:100%;
  height:350px;
  border-radius: 15px 15px 0px 0px;
}

.speaker-name
{
  
  font-size:28px;
  padding-top:15px;
  font-family: 'Muli', sans-serif;
  font-weight: bold;
  text-align: center;
  
}
.speaker-info
{
  display: flex;
  flex-direction: column;
}
.speaker-title
{
  font-size:20px;
  font-family: 'Work Sans', sans-serif;
  padding-top: 10px;
  padding-left:20px;
  font-style: italic;
   
}
.speaker-description
{
  
  font-size:26px;
  color:white;
  height: 100%;
  width:100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-style: italic; 
  font-weight: 300; 
  text-align: center;
  position: absolute;
  top:0px;
  opacity: 0;
  background: rgba(0,0,0,0.8);
  border-radius: 15px 15px 0px 0px;
}

.speaker-description:hover{
  opacity: 1;
}
.guest-area
{
  margin-top:40px;
  margin-bottom: 60px;
}
.title {
  text-align: justify;
}
.speaker-link:hover
{
  text-decoration: none;
}
.Guestlist-title {
  font-size: 20px;
  text-align: center;
}

/************************Sponsors****************************/
.sponsor-images {
  margin-left:50px;
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;
  object-fit: cover;
}

.sponsor-images img {
  transition: none;
}
.sponsor-image
{
   width:250px;
   height:250px;
   box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
   margin-left:40px;
   margin-bottom:60px;

}

.sponsor-images img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1)
}

/*------------------Prizes Area------------------------------*/
.Prize-list-title {
